.content {
  width: 100%;
  margin-top: 70px;
  padding: 30px;
}

.nav {
  position: fixed;
  top: 0;
  transition: transform 250ms ease-in;
  z-index: 2;
}

.nav.none {
  transform: translateX(-240px);
}

.nav.show {
  transform: translateX(0px);
}

.bg {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
